@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}

.MainHeader_header__1VYwk {
  width: 100%;
  height: 5rem;
  padding: 0 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #252424;
}

.MainHeader_header__1VYwk h1 {
  color: white;
}

.Card_card__2nKG4 {
  margin: 10px auto;
  border-radius: 6px;
  background-color: white;
  padding: 10px;
  width: 90%;
  max-width: 40rem;
  text-align: center;
}

.Questions_question__Pzvoy {
  margin: 2rem auto;
  width: 90%;
  max-width: 40rem;
}

.Questions_question__Pzvoy ul {
  list-style: none;
  padding: 1rem;
}

.Questions_question__Pzvoy li {
  border-radius: 3px;
  cursor: pointer;
  border: 1px solid #ccc;
  margin: 0.5rem 0;
  padding: 0.5rem;
}

.Questions_question__Pzvoy li:hover {
  background-color: rgb(182, 180, 180);
}
.Questions_question__Pzvoy li:active {
  background-color: #3f3f3f;
}

.Button_button__1Nk4s {
  margin-top: 10px;
  font: inherit;
  border: 1px solid #4f005f;
  background: #4f005f;
  color: white;
  padding: 0.25rem 1rem;
  cursor: pointer;
  border-radius: 5px;
}

.Button_button__1Nk4s:hover,
.Button_button__1Nk4s:active {
  background: #741188;
  border-color: #741188;
}

.Button_button__1Nk4s:focus {
  outline: none;
}

h4 {
  margin-top: 0px;
  margin-bottom: 10px;
}

.Settings_items__16xEE {
  width: 40%;
}

.Settings_item__QG_Ue {
  width: 20%;
  text-align: center;
  border-radius: 3px;
}

